.container {
  margin-right: auto;
  margin-left: auto;

  @media screen and (max-width: 1439px) {
    padding-right: calc(
      72.63px + (136 - 72.63) * ((100vw - 769px) / (1440 - 769))
    );
    padding-left: calc(
      72.63px + (136 - 72.63) * ((100vw - 769px) / (1440 - 769))
    );
    max-width: calc(769px + (1440 - 769) * ((100vw - 769px) / (1440 - 769)));
  }

  @media screen and (min-width: 1440px) {
    padding-right: 136px;
    padding-left: 136px;
    max-width: 1440px;
  }

