.btn {
  font-size: calc(8.54px + (16 - 8.54) * ((100vw - 769px) / (1440 - 769)));
  // line-height: calc(0.8px + (1.5 - 0.8) * ((100vw - 769px) / (1440 - 769)));
  letter-spacing: 0.06em;
  color: var(--white-txt-cl);
  background-color: #000000;
  border-radius: calc(6.94px + (13 - 6.94) * ((100vw - 769px) / (1440 - 769)));
  border: transparent;

  @media screen and (max-width: 1439px) {
    font-size: calc(8.54px + (16 - 8.54) * ((100vw - 768px) / (1440 - 768)));
    padding-top: calc(5.87px + (11 - 5.87) * ((100vw - 769px) / (1440 - 769)));
    padding-right: calc(
      10.68px + (20 - 10.68) * ((100vw - 769px) / (1440 - 769))
    );
    padding-bottom: calc(
      5.87px + (11 - 5.87) * ((100vw - 769px) / (1440 - 769))
    );
    padding-left: calc(
      10.68px + (20 - 10.68) * ((100vw - 769px) / (1440 - 769))
    );
  }

  @media screen and (min-width: 1440px) {
    font-size: 16px;
    padding-top: 11px;
    padding-right: 20px;
    padding-bottom: 11px;
    padding-left: 20px;
  }

  // @media screen and (min-width: 769px) and (max-width: 1440px) {
