  //   padding-right: calc(
  //     10.68px + (20 - 10.68) * ((100vw - 769px) / (1440 - 769))
  //   );
  //   padding-bottom: calc(
  //     5.87px + (11 - 5.87) * ((100vw - 769px) / (1440 - 769))
  //   );
  //   padding-left: calc(
  //     10.68px + (20 - 10.68) * ((100vw - 769px) / (1440 - 769))
  //   );
  // }

  // @media screen and (min-width: 1441px) {
  //   font-size: calc(16px + (21.33 - 16) * ((100vw - 1440px) / (1920 - 1440)));
  //   padding-top: calc(5.87px + (11 - 5.87) * ((100vw - 769px) / (1440 - 769)));
  //   padding-right: calc(
  //     10.68px + (20 - 10.68) * ((100vw - 769px) / (1440 - 769))
